import { Wheel } from 'react-custom-roulette'
import { useState } from 'react';

function PointRoulette() {
  //
  const data = [
    { option: '', style: { backgroundColor: 'gray', textColor: 'white' }, percentage : 50 },
    { option: 'x2', style: { backgroundColor: 'red', textColor: 'white'  }, percentage : 20 },
    { option: 'x5', style: { backgroundColor: 'blue', textColor: 'white'  }, percentage : 10 },
    { option: 'x20' , style: { backgroundColor: 'white', textColor: 'blue'  }, percentage : 1 },
    { option: '-x2' , style: { backgroundColor: 'black', textColor: 'yellow'  }, percentage : 10 },
    { option: '-x5' , style: { backgroundColor: 'black', textColor: 'pink'  }, percentage : 8 },
    { option: '-x100' , style: { backgroundColor: 'black', textColor: 'red'  }, percentage : 1 },
    ]

  
  const [mustSpin, setMustSpin] = useState(false);  //   
  const [prizeNumber, setPrizeNumber] = useState(0);// 

  const handleSpinClick = () => {
    if (!mustSpin) {
      // 1.   
      const pivot   = Math.floor((Math.random() * 99) +1);
      let   stack   = 0; // 

      let percentage = data.map((row,idx)=>{  { return row.percentage } });

      let newPrizeNumber = null; // 

      percentage.some((row,idx)=>{
        //2.  
        stack += row;

        // 3.      
        if(pivot <= stack){
          newPrizeNumber  = idx;
          return true;
        }

      }) 
      setPrizeNumber(newPrizeNumber);
      setMustSpin(true);
    }
  }

  const StopSpinning = () => {
    setMustSpin(false);
   // alert(data[prizeNumber].option+' ');
  }

  return (
    <div className="Roulette">
     <Wheel
        spinDuration={0.2} // spin
        startingOptionIndex={Math.floor(Math.random() * data.length)} 
        mustStartSpinning={mustSpin}
        prizeNumber={prizeNumber}
        data={data}
        onStopSpinning={StopSpinning}
      />
      <button onClick={handleSpinClick}>SPIN</button>
      <div>{prizeNumber}  </div>
    </div>
  );
}

export default PointRoulette;